import React from "react";
import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
  Button,
  Box,
  Typography,
} from "@mui/material";
import { Formik, Form } from "formik";

const SurveyForm = () => {
  return (
    <Formik
      initialValues={{ interested: "", role: "", renovation: "", cabinets: "" }}
      validate={(values) => {
        const errors = {};
        if (!values.interested) {
          errors.interested = "Please select an option";
        }
        if (!values.role) {
          errors.role = "Please select an option";
        }
        if (!values.renovation) {
          errors.renovation = "Please select an option";
        }
        if (!values.cabinets) {
          errors.cabinets = "Please select an option";
        }
        return errors;
      }}
      onSubmit={(values) => {
        console.log(values);
      }}
    >
      {({ values, handleChange, handleBlur, touched, errors, isValid }) => (
        <Form>
          <Typography variant="h5" gutterBottom>
            Would you be interested in taking part in the survey?
          </Typography>
          <FormControl component="fieldset">
            <RadioGroup
              name="interested"
              value={values.interested}
              onChange={handleChange}
              onBlur={handleBlur}
            >
              <FormControlLabel value="yes" control={<Radio />} label="Yes" />
              <FormControlLabel value="no" control={<Radio />} label="No" />
            </RadioGroup>
          </FormControl>
          {touched.interested && errors.interested && (
            <Typography variant="body2" color="error">
              {errors.interested}
            </Typography>
          )}

          <Typography variant="h5" gutterBottom>
            Based on the following options, how would you describe your role and
            responsibility in your household regarding decision making?
          </Typography>
          <FormControl component="fieldset">
            <RadioGroup
              name="role"
              value={values.role}
              onChange={handleChange}
              onBlur={handleBlur}
            >
              <FormControlLabel
                value="1"
                control={<Radio />}
                label="You are the main decision maker"
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                label="You have joint responsibility in the decision making process"
              />
              <FormControlLabel
                value="3"
                control={<Radio />}
                label="You are not involved in any way in the final decision-making process"
              />
              <FormControlLabel
                value="4"
                control={<Radio />}
                label="Ask to speak to the person responsible for decision making of the household and start from the beginning"
              />
            </RadioGroup>
          </FormControl>
          {touched.role && errors.role && (
            <Typography variant="body2" color="error">
              {errors.role}
            </Typography>
          )}

          <Typography variant="h5" gutterBottom>
            Which of the following best apply to you?
          </Typography>
          <FormControl component="fieldset">
            <RadioGroup
              name="renovation"
              value={values.renovation}
              onChange={handleChange}
              onBlur={handleBlur}
            >
              <FormControlLabel
                value="1"
                control={<Radio />}
                label="We have done some renovations in the past 6 months"
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                label="We are planning on making some renovations in the next 6 months"
              />
              <FormControlLabel
                value="3"
                control={<Radio />}
                label="We have built a new home in the past 6 months"
              />
              <FormControlLabel
                value="4"
                control={<Radio />}
                label="We are planning on building a new home in the next 6 months"
              />
              <FormControlLabel
                value="5"
                control={<Radio />}
                label="We have not made any renovations in the past 6 months and we are not planning of making any"
              />
              <FormControlLabel
                value="6"
                control={<Radio />}
                label="We have not built a new home in the past 6 months and are not planning on building any"
              />
            </RadioGroup>
          </FormControl>
          {touched.renovation && errors.renovation && (
            <Typography variant="body2" color="error">
              {errors.renovation}
            </Typography>
          )}
          {values.renovation === "1" ||
            values.renovation === "2" ||
            values.renovation === "3" ||
            (values.renovation === "4" && (
              <div>
                <Typography variant="h5" gutterBottom>
                  S3a. Did you install any cabinets during your renovation or
                  building process?
                </Typography>
                <FormControl component="fieldset">
                  <RadioGroup
                    name="installCabinets"
                    value={values.installCabinets}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  >
                    <FormControlLabel
                      value="1"
                      control={<Radio />}
                      label="Yes"
                    />
                    <FormControlLabel
                      value="2"
                      control={<Radio />}
                      label="No"
                    />
                  </RadioGroup>
                </FormControl>
                {touched.installCabinets && errors.installCabinets && (
                  <Typography variant="body2" color="error">
                    {errors.installCabinets}
                  </Typography>
                )}

                {values.installCabinets === "1" && (
                  <div>
                    <Typography variant="h5" gutterBottom>
                      S3b. Do you intend to install any cabinets during your
                      renovation or building process?
                    </Typography>
                    <FormControl component="fieldset">
                      <RadioGroup
                        name="intendInstallCabinets"
                        value={values.intendInstallCabinets}
                        onChange={handleChange}
                        onBlur={handleBlur}
                      >
                        <FormControlLabel
                          value="1"
                          control={<Radio />}
                          label="Yes"
                        />
                        <FormControlLabel
                          value="2"
                          control={<Radio />}
                          label="No"
                        />
                      </RadioGroup>
                    </FormControl>
                    {touched.intendInstallCabinets &&
                      errors.intendInstallCabinets && (
                        <Typography variant="body2" color="error">
                          {errors.intendInstallCabinets}
                        </Typography>
                      )}
                  </div>
                )}
              </div>
            ))}

          <Button variant="contained" color="primary" type="submit">
            Submit
          </Button>
        </Form>
      )}
    </Formik>
  );
};

export default SurveyForm;
